html {
    --main-red: rgb(222, 43, 43);
    --main-dark-red: rgb(157, 26, 26);
    --main-orange: rgb(243, 167, 26);
}

html, body, #root {
    width: 100%;
    height: 100%;
    margin: 0;
}

.App {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header {
    width: 100%;
    padding-top: 20px;
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    border-bottom: 2px solid var(--main-red);
}

.logo {
    cursor: pointer;
}

nav {
    position: relative;
    height: 80px;
    display: flex;
    align-items: flex-end;
    justify-content: space-around;
}

.nav-btn {
    width: 130px;
    height: 30px;
    margin-left: 40px;
    background-color: var(--main-red);
    color: var(--main-orange);
    font-size: 1.1em;
    font-weight: bolder;
    border: 2px solid var(--main-orange);
    border-bottom: none;
}

.nav-btn:hover {
    border: 2px solid var(--main-red);
    border-bottom: none;
    color: var(--main-red);
    background-color: var(--main-orange);
    cursor: pointer;
}

.nav-user {
    position: absolute;
    font-weight: bolder;
    color: var(--main-dark-red);
    right: 0;
    top: 0;
}

.nav-user-text:hover {
    color: var(--main-orange);
    cursor: pointer;
}

.nav-user-text {
    width: 200px;
    margin: 0;
    text-align: end;
}

.nav-user-logout {
    margin-top: 5px;
}

.main {
    padding-bottom: 100px;
}

.home {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    width: 70%;
}

.home-section:nth-child(2n) {
    background-color: var(--main-red);
    color: var(--main-orange);
}

.hero {
    display: flex;
    justify-content: center;
    align-items: center;
}

.hero-adopt {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 100px;
    color: var(--main-dark-red);
}

.home-learn {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.home-learn img {
    width: 60%;
    height: 80%;
    box-shadow: 0px 0px 4px 4px var(--main-orange);
}

.home-learn-text {
    display: flex;
    flex-direction: column;
    width: 30%;
    height: 80%;
    font-size: 1.2em;
}

.home-btn {
    width: 150px;
    height: 40px;
    background-color: var(--main-orange);
    color: var(--main-red);
    font-size: 1em;
    font-weight: bolder;
    border: 2px solid var(--main-dark-red);
}

.home-btn:hover {
    color: var(--main-orange);
    background-color: var(--main-red);
    cursor: pointer;
}

.about {
    width: 70%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    margin: 30px;
}

.about img {
    width: 40%;
    box-shadow: 0px 0px 3px 3px var(--main-dark-red);
}

.about-text {
    width: 40%;
    font-size: 1.2em;
}

.browse {
    width: 65%;
    max-height: 600px;
    overflow: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid black;
    margin: 30px;
    background-color: var(--main-red);
    color: var(--main-orange);
    font-weight: bolder;
    padding: 50px;
    padding-top: 0px;
}

.browse-search {
    width: 90%;
    display: flex;
    justify-content: flex-end;
}

.browse-listings {
    width: 100%;
    display: grid;
    gap: 40px;
    grid-template-columns: repeat(2, 1fr);
}

.listing {
    display: flex;
    height: 150px;
    margin-bottom: 10px;
    margin-top: 10px;
    padding: 10px;
    background-color: var(--main-orange);
    color: var(--main-dark-red);
}

.listing:hover {
    background-color: white;
    cursor: pointer;
}

.listing-pic {
    width: 150px;
    display: flex;
    justify-content: center;
}

.listing-pic-img {
    height: 100%;
    max-width: 100%;
}

.listing-info {
    margin-left: 10px;
    height: 100%;
}

.listing-section {
    height: 25%;
}

.login {
    width: 75%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    padding-top: 50px;
}

.login h2 {
    width: 100%;
    text-align: center;
}

.login-section {
    margin-top: 50px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 20px;
    background-color: var(--main-red);
    color: var(--main-orange);
    font-weight: bolder;
    font-size: 1.1em;
    border: 1px solid red;
}

.login-label {
    margin-bottom: 10px;
}

.login-label input {
    margin-left: 10px;
    width: 300px;
    height: 30px;
}

.login-checkbox-container {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-checkbox-container label {
    margin: 0;
}

.login-checkbox {
    width: 20px;
    height: 20px;
}

.login-checkbox input {
    margin: 0;
}

.profile {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile-container {
    width: 85%;
    height: 90%;
    margin-top: 50px;
    background-color: var(--main-dark-red);
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0;
}

.profile-sidebar {
    width: 35%;
    height: 90%;
    background-color: var(--main-red);
    display: flex;
    flex-direction: column;
}

.profile-pic {
    margin-top: 20px;
    margin-bottom: 20px;
    height: 200px;
    width: 200px;
    border: 2px solid black;
    border-radius: 50%;
    align-self: center;
}

.profile-content {
    width: 60%;
    height: 90%;
    background-color: var(--main-red);
}

.profile-data-show-container {
    width: 95%;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: 5px;
    margin-right: 5px;
}

.profile-data-show-text {
    width: 85%;
    display: flex;
    align-items: center;
    color: var(--main-orange);
    font-weight: bolder;
    margin-left: 10px;
    margin-right: 10px;
}

.profile-data-show-text p {
    overflow: auto;
}

.profile-data-show-container:hover .profile-data-show-btn {
    visibility: visible;
    cursor: pointer;
}

.profile-data-show-btn {
    visibility: hidden;
    background-color: var(--main-red);
    border: none;
    color: var(--main-orange);
}

.profile-sidebar-edit-btn {
    background-color: var(--main-red);
    border: none;
    color: var(--main-orange);
    font-weight: bolder;
}

.profile-sidebar-edit-btn:hover {
    color: var(--main-red);
    background-color: var(--main-orange);
    cursor: pointer;
}

.profile-data-show-text label {
    margin: 0;
    margin-right: 10px;
    font-size: 1.1em;
}

.profile-data-show-text p {
    margin: 0;
    font-size: 1.1em;
}

.profile-sidebar-info-item {
    width: 95%;
    display: flex;
    margin-right: 5px;
    margin-left: 15px;
}

.profile-sidebar-info-item input {
    width: 80%;
}

.footer {
    width: 100%;
    height: 50px;
    display: flex;
    position: fixed;
    bottom: 0;
    justify-content: center;
    align-items: center;
    background-color: var(--main-red);
    border-top: 2px solid var(--main-orange);
    color: var(--main-orange);
    font-weight: bolder;
    margin-top: 20px;
}